<template>
  <div class="main">
    <van-nav-bar title="" left-arrow @click-left="$router.go(-1)" />
    <div v-for="item in list" :key="item.id">
      <div class="list">
        <div class="button">
          <van-button type="info" @click="del(item.id)"></van-button>
        </div>
        <p class="name">:{{item.order_no}}</p>
        <p class="name">:{{item.goods_name}} :{{item.price}} :{{item.number}}</p>
        <p class="name">:{{item.number * item.price}}  : {{item.status?'':''}}</p>
      </div>
    </div>
  </div>
</template>
<script>
import { getOrder, delOrder } from "../../utils/local-data";
import { getCookie, setCookie } from "@/utils/local-data";
export default {
  data() {
    return {
      list: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      getOrder(getCookie("COOKIE_USER_X_ID")).then(res => {
        if (res.status == 200 && res.data.status == 0) {
          this.list = res.data.data;
        }
      });
    },
    del(id) {
      this.$dialog.confirm({
        title: "",
        message: ""
      })
        .then(() => {
          delOrder(id).then(res => {
        if (res.status == 200 && res.data.status == 0) {
          this.$toast(res.data.msg);
          location.reload();
        }
      });
        })
        .catch(() => {
          // on cancel
        });
     
    }
  }
};
</script>
<style scoped lang = "scss">
.list {
  height: 70px;
  position: relative;
}
.name {
  text-indent: 10px;
  line-height: 20px;
  font-size: 15px;
}
.button {
  float: right;
}
</style>